$first-color: #0C0032;						
$second-color: #464866;		
$third-color: #211A36;	
$contact-form: #edf5e1;
$main-font: Georgia, 'Times New Roman', Times, serif;
$hover:#659DBD;
$hover-menu:#FF0700;
$text-color:white;

.projects {
  padding: 35px 0 30px;
  margin: auto;

    &__portfolio {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        row-gap: 30px;
        column-gap: 60px;
    }

    &__portfolio__item {
        background-color: $second-color;
        border-bottom-style: solid;
        border-bottom-color: $hover-menu;
        border-radius: 10px;
        h3 {
            text-align: center;
            padding-top: 10px;
            color: $text-color;
        }
        p {
            padding: 10px 20px;
            text-align: left;
        }
    }

    &__portfolio__socialmedia {
        font-weight: bold;
        font-size: 40px;
        text-align: center;
        padding-bottom: 10px;
        a {
            color:$text-color;
            cursor: pointer;
            &:hover {
                color: $hover-menu;
            }
        }
    }

    &__portfolio__flipbox {
        max-width: 200px;
        height: 200px;
        margin: auto;
    }
    
    &__portfolio__flipbox__inner {
        position: relative;
        width: 100%;
        height: 100%;
        text-align: center;
        transition: transform 0.8s;
        transform-style: preserve-3d;
    }
    
    &__portfolio__flipbox:hover &__portfolio__flipbox__inner {
        transform: rotateY(180deg);
    }
    
    &__portfolio__flipbox__front1, &__portfolio__flipbox__front2, &__portfolio__flipbox__front3, &__portfolio__flipbox__back {
        position: absolute;
        width: 100%;
        height: 100%;
        border-radius: 10px;
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
    }
    
    &__portfolio__flipbox__front1 {
        background-color: #d6c6b4;
        z-index: 1; //so pointer work properly on the back side
        cursor: pointer;
    }

    &__portfolio__flipbox__front2 {
        background-color:#116466;
        z-index: 1; //so pointer work properly on the back side
        cursor: pointer;
    }

    &__portfolio__flipbox__front3 {
        background-color:$text-color;
        z-index: 1; //so pointer work properly on the back side
        cursor: pointer;
    }
    
    &__portfolio__flipbox__back {
        background-color: $hover;
        transform: rotateY(180deg);
        z-index: 2; //so pointer work properly on the back side
        a {
            color:$text-color;
            font-weight: bold;
            font-size: 25px;
            margin: auto;
            position: relative;
            top: 30%;
            &:hover {
                color: $hover-menu;
            }
        }
    }

    &__portfolio__logo1,  &__portfolio__logo2 {
        max-width: 70%;
        position: relative;
        top: 15%;
    }
     
    .pulse img {
        animation-name: pulse;
        animation-duration: 3s;
        animation-iteration-count: infinite;
    }

    @keyframes pulse {
        0% {transform: scale(1);}
        50% {transform: scale(1.3);}
        100% {transform: scale(1);}
    }
}

@media only screen and (max-width: 1560px) {

.projects__portfolio {
    grid-template-columns: 1fr 1fr;
}
}
  
@media only screen and (max-width: 1080px) {

.projects__portfolio {
    grid-template-columns: 1fr;
    margin: auto 100px;
}
}

@media only screen and (max-width: 900px) {

.projects {
    padding: 25px 0 30px;
  
    &__portfolio {
        margin: auto 20px;
    p {
        text-align: left;    
    }
  }
}
}

@media only screen and (max-width: 700px) {

.projects {
    padding: 20px 0;
    margin: auto;

    &__portfolio {
        margin: auto 50px;
        p {
            text-align: left;    
        }
    }

    &__portfolio__item {
        h3 {
            font-size: 20px;
        }
    }
}

@media only screen and (max-width: 500px) {

.projects__portfolio {
    margin: auto 25px;
}

.projects__portfolio__flipbox {
    max-width: 150px;
    height: 150px;
    margin: auto;
}

.projects__portfolio__flipbox__back {
    a {
        position: relative;
        top: 25%;
        font-size: 18px;
        &:hover {
            color: $text-color;
            filter: none;
            transform: none;
            &:active {
                color: $hover-menu;
            }
        }
    }
} 
}
}